.resume-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 800px;
  /* width: auto; */
  margin: 4rem 0;
  padding: 0 14rem;
}

.resume-container iframe {
  /* aspect-ratio: 8.5 / 11; */
  width: 80%;
  height: 100%;
}

@media screen and (max-width: 1450px) {
  .resume-container {
    padding: 0 2rem;
  }
}

@media screen and (max-width: 880px) {
  .resume-container {
    padding: 0;
    margin: 2rem 0;
  }
  .resume-container iframe {
    width: 90%;
  }
}

@media screen and (max-width: 670px) {
  .resume-container {
    height: 650px;
  }
}

@media screen and (max-width: 530px) {
  .resume-container {
    height: 550px;
  }
  .resume-container iframe {
    width: 100%;
  }
}
