.not-found-container {
  display: flex;
  height: 75vh;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  width: auto;
}

.not-found-text-container {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #5c5c5c;
  font-family: "Inconsolata", monospace;
  text-align: center;
  /* font-family: "Lato", sans-serif; */
}

.not-found-container h1 {
  font-size: 240px;
  line-height: 200px;
  letter-spacing: -8px;
  color: rgb(171, 171, 171);
  margin: 0;
  /* font-family: "Lato", sans-serif; */
  font-weight: 400;
}

.not-found-container h2 {
  font-size: 28px;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;
}

.not-found-container p {
  font-size: 18px;
  text-transform: uppercase;
  /* width:30%; */
}

@media screen and (max-width: 1220px) {
  .not-found-container h1 {
    font-size: 216px;
    line-height: 188px;
  }

  .not-found-text-container {
    width: 40%;
  }
}

@media screen and (max-width: 800px) {
  .not-found-text-container {
    width: 60%;
  }
}

@media screen and (max-width: 600px) {
  .not-found-container h1 {
    font-size: 180px;
    line-height: 160px;
  }

  .not-found-container h2 {
    font-size: 24px;
  }

  .not-found-container p {
    font-size: 16px;
  }
}

@media screen and (max-width: 450px) {
  .not-found-text-container {
    width: 80%;
  }
  .not-found-container h1 {
    font-size: 132px;
    line-height: 120px;
  }

  .not-found-container h2 {
    font-size: 24px;
  }

  .not-found-container p {
    font-size: 16px;
  }
}
